.movie-big-container {
    position: relative;
    display: flex;
    justify-content: center;
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 110vh;
    padding-top: 30px;
  }
  .movie-app-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    color: white;
    font-family: arial, sans-serif;
    line-height: 1.8em;
    width: 60vw;
    position: absolute;
  }
  
  .movie-top-container {
    display: flex;
    justify-content: space-between;
    background-color: transparent;
    color: white;
    margin-bottom: 20px;
  }
  .selectoption {
    width: 400px;
  }
  .movie-container {
    display: flex;
    background-color: rgb(5, 2, 14, 0.9);
  }
  
  .context {
    display: flex;
    flex-direction: column;
    padding: 10px;
  }
  
  .title {
    text-transform: uppercase;
    font-size: 2.5em;
    color: white;
    line-height: 1em;
  }
  
  .tag-line {
    font-size: 1.3em;
    color: #00fc87;
  }
  
  .movie-detail {
    color: rgb(236, 230, 230);
  }
  
  .genres,
  .date,
  .mins,
  .revenue,
  .rate {
    color: #00fc87;
    font-size: 1.4em;
  }
  
  .small-detail {
    display: flex;
    flex-direction: column;
    width: 50%;
  }
  .small-detail-container {
    display: flex;
  }
  